@import "./mixin";
@import "./variables_color";

@media screen and(max-width: 450px) {
  .toolbar_navigations_items {
    display: none;
  }
  @include nav_menu_components_phone_and_tablet();
  .opening_times {
    span {
      font-size: 50%;
      color: red;
    }
  }
  .background_contacts, 
  .main_contacts,
  .background_image_cart,
  .box_infoGame {
    margin: 0;
  } 
  .center_image_home {    
    img {
      height: 50vh;
      width: 100%;
    }
  }
  .links_products_home {
    background-color: #e0e0e0; 
    display: flex;
    flex-direction: column;
    .link_item_product {
      margin: 0 auto;
      img {
        height: 60vh;
      }
    }
  }
 
  .error_box {
    h1 {
      font-size: 60px;
    }
  }
  .img_error_box {
    img {
      width: 100px;
    }
  }
  .current_component_clicked_on_parrent {
    margin-top: 2rem; 
    .name_child {
      a {
        font-size: 16px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 16px;
      }
    }
  }
}

@media screen and(min-width: 451px) and(max-width: 768px) {
  .toolbar_navigations_items {
    display: none;
  }

  @include nav_menu_components_phone_and_tablet();
  
  .opening_times {
    span {
      font-size: 70%;
      color: red;
    }
  }
  .background_contacts,
  .box_infoGame {
    margin: 0;
  }
  .main_contacts {
    margin: 0 1rem;
  }

  .tollbar_toggle_button {
    display: show;
  }
  .center_image_home {
    img {
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      height: 50vh;
      width: 100%;
    }
  }
  .links_products_home {
    display: flex;

    .link_item_product {
      width: 100vw;
      img {
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        width: 100%;
        height: 50vh;
      }
    }
  }
  .error_box {
    h1 {
      font-size: 140px;
    }
  }
  .current_component_clicked_on_parrent {
    margin: 2rem; 
    .name_child {
      a {
        font-size: 19px;
      }
    }
    .nameInfo_current_component_childs {
      h3 {
        font-size: 19px;
      }
    }
      
  }
}
 
